header {
  flex-direction: column;
  gap: clamp(.5rem, 2vw, 1rem);
  display: flex;
}

header > h1 {
  color: var(--gray-50);
  text-shadow: 0px 2px 4px #0000002e, 4px 4px 0px var(--accent);
  font-size: clamp(4rem, 20vw, 8rem);
  font-weight: 600;
